'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { registerUser } from '@/lib/api'
import { registerValidator } from '@/lib/validators'

export default function RegisterPage() {
  const router = useRouter()
  const [form, setForm] = useState({
    first_name: '',
    surname: '',
    other_names: '',
    email: '',
    pen_number: '',
    password: '',
    confirm_password: '',
  })
  const [errors, setErrors] = useState<Record<string, string>>({})
  const [submitting, setSubmitting] = useState(false)
  const [penFeedback, setPenFeedback] = useState('')

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setForm((prev) => ({ ...prev, [name]: value }))
    setErrors((prev) => ({ ...prev, [name]: '' }))
  }

  const checkPenNumberExists = async (pen_number: string) => {
    const existingPENs = ['PEN12345', 'PEN67890']
    if (existingPENs.includes(pen_number)) {
      setPenFeedback('This PEN Number already exists.')
    } else {
      setPenFeedback('')
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setSubmitting(true)
    setErrors({})
    setPenFeedback('')

    const validationErrors = registerValidator(form)
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors)
      setSubmitting(false)
      return
    }

    try {
      await registerUser({
        first_name: form.first_name,
        surname: form.surname,
        other_names: form.other_names,
        email: form.email,
        pen_number: form.pen_number,
        password: form.password,
      })
      router.push('/login')
    } catch (err: any) {
      if (err.message.includes('User already exists')) {
        setPenFeedback('This PEN Number or Email already exists.')
      }
    } finally {
      setSubmitting(false)
    }
  }

  return (
    <div className="max-w-md mx-auto bg-white rounded-lg shadow p-6 mt-10">
      <h2 className="text-2xl font-semibold text-center text-blue-700 mb-6">User Registration</h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="font-medium">First Name</label>
          <input
            type="text"
            name="first_name"
            value={form.first_name}
            onChange={handleChange}
            className="w-full p-2 border rounded"
          />
          <p className="text-red-500 text-sm">{errors.first_name}</p>
        </div>

        <div>
          <label className="font-medium">Surname</label>
          <input
            type="text"
            name="surname"
            value={form.surname}
            onChange={handleChange}
            className="w-full p-2 border rounded"
          />
          <p className="text-red-500 text-sm">{errors.surname}</p>
        </div>

        <div>
          <label className="font-medium">Other Names (Optional)</label>
          <input
            type="text"
            name="other_names"
            value={form.other_names}
            onChange={handleChange}
            className="w-full p-2 border rounded"
          />
        </div>

        <div>
          <label className="font-medium">Email</label>
          <input
            type="email"
            name="email"
            value={form.email}
            onChange={handleChange}
            className="w-full p-2 border rounded"
          />
          <p className="text-red-500 text-sm">{errors.email}</p>
        </div>

        <div>
          <label className="font-medium">PEN Number</label>
          <input
            type="text"
            name="pen_number"
            value={form.pen_number}
            onChange={handleChange}
            onBlur={() => checkPenNumberExists(form.pen_number)}
            className="w-full p-2 border rounded"
          />
          <p className="text-red-500 text-sm">{penFeedback || errors.pen_number}</p>
        </div>

        <div>
          <label className="font-medium">Password</label>
          <input
            type="password"
            name="password"
            value={form.password}
            onChange={handleChange}
            className="w-full p-2 border rounded"
          />
          <p className="text-red-500 text-sm">{errors.password}</p>
        </div>

        <div>
          <label className="font-medium">Confirm Password</label>
          <input
            type="password"
            name="confirm_password"
            value={form.confirm_password}
            onChange={handleChange}
            className="w-full p-2 border rounded"
          />
          <p className="text-red-500 text-sm">{errors.confirm_password}</p>
        </div>

        <button
          type="submit"
          disabled={submitting}
          className={`w-full p-2 rounded text-white ${
            submitting ? 'bg-gray-400' : 'bg-blue-600 hover:bg-blue-700'
          }`}
        >
          {submitting ? 'Registering...' : 'Register'}
        </button>
      </form>

      <p className="text-center text-xs mt-6 text-gray-500">
        &copy; 2025 Prime Pillars Real Estate
      </p>
    </div>
  )
}
